<template>
  <v-container>
    <v-layout justify-center>
      <v-flex>
        <v-card>
          <v-card-title primary-title>
            <v-layout justify-center>
              <h2 class>Die Suche nach dem Geheimnis von Republika</h2>
            </v-layout>
          </v-card-title>

          <v-stepper v-model="e1">
            <v-stepper-header>
              <v-stepper-step :complete="e1 > 0" step="0">Prolog</v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 1" step="1"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 2" step="2">Kapitel 1</v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 3" step="3"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 4" step="4"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 5" step="5"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 6" step="6">Kapitel 2</v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 7" step="7"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 8" step="8"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 9" step="9"></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step :complete="e1 > 10" step="10">Kapitel 3</v-stepper-step>
            </v-stepper-header>

            <v-stepper-items>
              <v-stepper-content step="0">
                <prolog :nextStep="nextStep" :toStep="toStep"></prolog>
              </v-stepper-content>
              <v-stepper-content step="1">
                <chapter1 :nextStep="nextStep"></chapter1>
              </v-stepper-content>
              <v-stepper-content step="2">
                <chapter2 :nextStep="nextStep"></chapter2>
              </v-stepper-content>
              <v-stepper-content step="3">
                <chapter3 :nextStep="nextStep"></chapter3>
              </v-stepper-content>
              <v-stepper-content step="4">
                <chapter4 :nextStep="nextStep"></chapter4>
              </v-stepper-content>
              <v-stepper-content step="5">
                <chapter5 :nextStep="nextStep"></chapter5>
              </v-stepper-content>
              <v-stepper-content step="6">
                <chapter6 :nextStep="nextStep"></chapter6>
              </v-stepper-content>
              <v-stepper-content step="7">
                <chapter7 :nextStep="nextStep"></chapter7>
              </v-stepper-content>
              <v-stepper-content step="8">
                <chapter8 :nextStep="nextStep"></chapter8>
              </v-stepper-content>
              <v-stepper-content step="9">
                <chapter9 :nextStep="nextStep"></chapter9>
              </v-stepper-content>
              <v-stepper-content step="10">
                <chapter10 :nextStep="nextStep"></chapter10>
              </v-stepper-content>
            </v-stepper-items>
          </v-stepper>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import Prolog from "@/components/Prolog";
import Chapter1 from "@/components/Chapter1";
import Chapter2 from "@/components/Chapter2";
import Chapter3 from "@/components/Chapter3";
import Chapter4 from "@/components/Chapter4";
import Chapter5 from "@/components/Chapter5";
import Chapter6 from "@/components/Chapter6";
import Chapter7 from "@/components/Chapter7";
import Chapter8 from "@/components/Chapter8";
import Chapter9 from "@/components/Chapter9";
import Chapter10 from "@/components/Chapter10";

export default {
  components: {
    Prolog,
    Chapter1,
    Chapter2,
    Chapter3,
    Chapter4,
    Chapter5,
    Chapter6,
    Chapter7,
    Chapter8,
    Chapter9,
    Chapter10,
  },
  data: () => ({
    e1: 0,
    maxChapter: 15
  }),
  methods: {
    nextStep: function() {
      this.e1 = +this.e1;
      if (this.e1 < this.maxChapter) {
        this.e1 = this.e1 + 1;
      }
    },
    toStep: function(chapter) {
      if (chapter > 0 && chapter <= this.maxChapter) {
        this.e1 = chapter;
      }
    }
  }
};
</script>

