<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-text>{{ unterkapitel4 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel5 }}</v-card-text>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Der Tempel der Mondgöttin",
    unterkapitel1:
      'In Serenity Valley angekommen ging ich ins Rathaus um eine Karte der Stadt zu bekommen. Am Schalter standen 2 Frauen und unterhielten sich. Da Sie aussahen als würden Sie hier Arbeiten sprach ich Sie an. “Hallo, dürfte ich fragen, wo ich eine Karte der Stadt bekommen kann?”  Eine der Frauen drehte sich mir zu und begann: “Hey wir unterhalten uns ge-…” Die andere unterbrach sie. “Cait entspann dich. Dein Chi gerät aus den Fugen.” Sie wandte sich mir zu und lächelte freundlich. “Hallo, ich bin Bürgermeisterin Rabecha. Karten haben wir hier nicht. Aber ich kenn mich gut aus. Willst du mich etwas fragen?” Überrascht von den vielen hochrangigen Persönlichkeiten mit denen ich heute zu tun hatte stammelte ich. “Äh-… ich suche Bögen unter Wasser.” Sie guckte verdutzt. “Du suchst sicher unsere Wasserkapelle. Sie ist draußen vor der Küste.” Sie beschrieb mir die genaue Lage, ich bedankte mich herzlich und ich machte mich auf den Weg, die Karte zu suchen.',
    unterkapitel2: 'Auf dem Weg zum Tempel las ich in Walters Tagebuch. Er schrieb nur sehr belanglos über seine Reise. Kaum ein Hinweis ließ sich seinen Aufzeichnungen entnehmen. Ob er absichtlich keine Informationen preisgab? Eine ganze Seite nutzte er für die Beschreibung der Stadttauben. Doch selbst rückwärts gelesen fand ich keine Hinweise. ',
    unterkapitel3: 'Tief unter dem Ozean nahe Serenity Valley bekam ich ein ungutes Gefühl auf meiner Nussschale, die ich beim Karten spielen in Tentacula gewonnen hatte. Das Holz vibrierte unter meinen Füßen. "Eine Priese Schicksal!" Hauchte ein kaltes flüstern aus den Tiefen, kurz bevor mein Boot in tausend Einzelteile zerbarst und ich nahezu im freien Fall im Wasser versank. Der Schrecken der Tiefe erfasste mich kurz, doch ich besann mich auf mein Bändigen und beruhigte mich um Luft zu sparen. Ich trieb schon sehr tief unter der Oberfläche. Nur noch wenig Licht kam von Oben bei mir an. Ich musste mindestens 100 Meter widerstandslos gesunken sein. Ich sah nach unten und bemerkte ein sanftes grünes glühen, verteilt über den Meeresboden soweit ich durch die Tiefe sehen konnte. Ich nutze mein Bändigen um schnell tiefe zu gewinnen, ohne dass mich der Tiefenrausch überwältigen konnte. Immer Größer wurde die Struktur. Immer differenzierter die Leuchtpunkte, bis ich direkt über einer riesigen Tempelanlage trieb. Ich konnte nicht ausmachen wie groß die Anlage insgesamt war, doch ich sah einen Eingang in das größte Gebäude. Eine riesige Pyramide mit Balkonen und Reliefs. Beleuchtet von tausenden weiß-grünen Laternen, die wohl nie aufhörten zu leuchten. ',
    unterkapitel4: 'Ich tauchte hinein. Die Suche nach dem Relikt begann.',
    unterkapitel5: 'Ich tauchte auf. Die Tiara der Mondgöttin in meiner Hand. Ich hatte es geschafft! Ich spürte ein glühen in meinen Venen dort wo ich es nur spürte, wenn ich die stärksten meiner Bändigerfähigkeiten nutzte. Es war ein großartiges motivierendes Gefühl. Ich war so begeistert von meinem Erfolg, dass ich mich auf das Wasser erhob und direkt in die Richtung der Luftnomaden surfte. Nun wollte ich auch das Relikt des ersten Luftbändigers!',
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "VElBUkE=") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

<style>
</style>
