<template>
  <v-flex>
    <v-layout justify-center>
      <v-card flat max-width="500">
        <v-card-title>
          <h2 class="headline">{{headline}}</h2>
        </v-card-title>
        <v-card-text>{{ unterkapitel1 }}</v-card-text>
        <v-card-text>{{ unterkapitel2 }}</v-card-text>
        <v-card-text>{{ unterkapitel3 }}</v-card-text>
        <v-card-text>{{ unterkapitel4 }}</v-card-text>
        <v-card-text>{{ unterkapitel5 }}</v-card-text>
        <v-card-text>{{ unterkapitel6 }}</v-card-text>
        <v-card-actions>
          <v-text-field
            label="Lösungswort:"
            :success="answered"
            clearable
            :rules="[validate]"
          ></v-text-field>
        </v-card-actions>
        <v-card-text v-if="answered">{{ unterkapitel7 }}</v-card-text>
        <v-card-text v-if="answered">{{ unterkapitel8 }}</v-card-text>
        <v-card-actions>
          <v-btn :disabled="!answered" flat color="orange" @click="nextStep">>> Weiter</v-btn>
        </v-card-actions>
      </v-card>
    </v-layout>
  </v-flex>
</template>

<script>
import Vue from 'vue';

export default {
  props: ["nextStep"],
  data: () => ({
    answered: false,
    headline: "Das Relikt des ersten Feuerbändigers",
    unterkapitel1: 'Das nächste Kapitel im Buch der 4 Relikte handelte von der Feuernation. Eine feindseelige Region. Karge Ländereien, brodelnde Lavaseen, verdorrte Pflanzen und strenge Menschen. Doch trotzdem freute ich mich. Nirgendwo sonst gab es so viele Badehäuser an heißen Quellen mit erfrischenden Getränken.',
    unterkapitel2: 'Unten sah ich eine reisende Feuerbändigerin. Ich erkannte sie an seiner Kleidung. Sie hatte ein freundliches Gesicht und sah gut gelaunt aus, also sprach ich sie an. “Hallo feurige Schönheit, können Sie mir den schnellsten Weg nach Lavandia nennen?” Sie lachte laut auf. “Feurige Schönheit? Du bist ja süß. Ich kann dir den Weg durch den Nether verraten. Dann bist du im nu da.” sie grinste. “Oder tot.” Antwortete ich lachend, mit allem Galgenhumor den ich aufbringen konnte. Sie fuhr fort: “Du kannst natürlich auch ein Schiff nehmen, du cooler Wasserkrieger. Aber wenn ich als zierliches Mädchen durch den Nether reise, was macht das dann aus dir?” Ich mochte ihre provokante Art und entgegnete. “Da hast du natürlich recht. Damit könnte ich nicht leben. Ich weiß ich werde es bereuen, aber willst du mir den Weg zeigen?” “Klar könnte ich das. Wenn du mir sagen kannst, wie das Feuerbändigen entstanden ist.',
    unterkapitel3: 'Sie willigte also ein und wir sprachen auf dem Weg zum Portal der Feuergeisterwelt über alles was uns in den Sinn kam. Ihr Name war Shikari Feuergast. Ihr Vater war ein Chiblocker und Ihre Mutter eine Feuerbändigerin. Sie reiste viel durch die Welt und erkundete Städte, schloss sich anderen reisenden an und half Menschen wo sie nur konnte. Sie war groß, hatte Sommersprossen, kurze schwarze Haare und Wache grüne Augen. “Wir sind da. Das Portal zur Feuergeisterwelt. Oder einfach gesagt der Nether, Wir sehen uns drüben” Sie sah mich zögern und schubste mich in die wabernd lilane Wand. Mit nur einem blinzeln verschwand die grüne Welt an diesem schönen Sommertag und wich einem dunklen Raum. Rote Wände und Luft, die beim Einatmen brannte wie Feuer, umgaben mich nun. Wieder wurde ich geschubst. “Komm schon. Wer hier trödelt, trödelt das letzte Mal.” Sie grinste vergnügt, packte meine Hand und zog mich hinter sich her. Meine Haut brannte am ganzen Körper. Schweiß verdampfte bevor er die Poren verließ. Warum war es hier so unglaublich heiß? Am Ende des Korridors öffnete sich ein riesiger Raum vor uns. Alles glühte orange. Dann begriff ich. Das glühen kam von einem Ozean aus Lava. Wir gingen durch eine riesige Höhle aus rotem Gestein. Ich blickte zurück. Mein Körper wollte umdrehen. Flucht. “Hey du schaffst das schon. Wir müssen nicht weit. Entfernungen sind hier schnell zu überwinden.” Shikari legte mir den Arm um die Hüfte. Meine Angst verflog und ich nahm einen tiefen schmerzhaften Atemzug. Ich sah einen schmalen Pfad direkt durch den Ozean aus Lava führen. “Bleib dicht bei mir.” Sie ging vor und zog mich sanft hinterher. Der Pfad hatte immer mehr Lücken. “Spring mir hinterher. Du schaffst das! Geübt und nahezu mühelos sprang sie von einem Vorsprung zum anderen und ich folgte ihr.',
    unterkapitel4: '"Ich sah das Portal und fing sofort an zu rennen. Ich musste raus. Ein blinzeln und ich stand in einem Haus aus Sandstein. Die Hitze verflog. Es war geschafft. Ich machte einen Schritt zur Seite und Shikari erschien aus dem Portal mit den Händen nach vorn gestreckt. “Oh” Sagte sie verwundert und grinste. “Da war garkeiner zum schubsen”. Ich lachte auch. Quatschend liefen wir in der Dunkelheit auf eine große Pyramide zu, die mit leuchtenden Steinen geschmückt war. Die Wege die wir nach einiger Zeit erreichten waren nicht wie sonst von Straßenlaternen oder Feuerkäferlampen beleuchtet. Stattdessen hatte man an beiden Seiten Streifen aus Flammengestein eingelassen, die nachts angeheizt vom Tag glühten. Je näher wir der Pyramide kamen, desto mehr Soldaten hielten sich an den Wegesrändern auf. “Das Rätzel wovon ich dir erzählt habe... Hast du eine Idee dazu?” Fragte ich Shikari. Sie nickte. “Naja du brauchst 4 Schlüsselwörter, die du im Nether nutzen kannst, um einen Ring zu bekommen. Und die sind in 4 Städten der Feuernation versteckt. Das ist doch ganz einfach.” “Jaaa so ähnlich hätte ich das auch gedacht” Antwortet ich etwas verlegen. Sie grinste hämisch. “Ist ja nicht so, als wärst du der Erste der nach dem Sonnenring sucht. Der Walter von dem du das Tagebuch hast, hat meinen Vater damals das gleiche gefragt. Was aus Ihm geworden ist, weiß ich aber nicht.” Mir stockte der Atem und entsetzt purzelten mir die Worte aus dem Mund. “Warum sagst du das nicht gleich?” Sie antwortete nur: “Du hast nicht gefragt.”',
    unterkapitel5: 'Am Fuß der Pyramide angekommen, stand eine Gruppe hochrangiger Feuerbändiger im Weg und schienen auf jemanden zu warten. Ich ging zur Seite, doch Shikari lief genau auf sie zu. Ich blieb stehen und traute meinen Augen nicht, denn nun fiel mir auf, dass der Mann in der Mitte die Krohne der Feuernation trug. Es war Feuerlord Rango. “General Feuergast, haben Sie die Informationen an das Luftkönigreich überbracht?” Shikari antwortete. “Ja Feuerlord Rango hier ist die Antwort.” Sie holte die versiegelte Schriftrolle hervor, die sie völlig unbeachtet von mir, an ihrem Gürtel trug. “Ich bin mit diesem Wasserbändiger angereist.” Sie nickte zu mir hinüber und ich nahm unwillkürlich Haltung an und entgegnete: “Ja so ist es euer Majestät.” Feuerlord Rango sah mich mit durchdringendem Blick an und wandte sich dann wieder Shikari zu. “Gut. Ich erwarte ihren Bericht zum Sonnenuntergang.” Die Delegation machte sich dann wieder auf den Weg in die Pyramide.',
    unterkapitel6: '“Los lass uns Schätze suchen.” Sagte Shikari vergnügt, als die Gruppe weit genug entfernt war. “General Feuergast?” Fragte ich sie völlig verwirrt. Sie lachte laut auf. “Für dich nur Shikari.” Und unsere Suche begann.',
    unterkapitel7: 'Shikari wirkte garnicht wie ein General. Sie war quirlig und herzlich. Der Sonnenschein in Person. Und doch wirke sie nun ein bisschen gefährlich.',
    unterkapitel8: 'Wir standen wieder vor dem lilanen Portal “Komm. Die Stelle im Nether, wo wir die Wörter brauchen kenne ich.” Nochmal in den Nether zu gehen, war das schlimmste, was ich mir vorstellen konnte. “Ich schaff das nicht nochmal aus eigener Kraft.” sagte ich, stellte mich nahe an das Portal und fragte sie:”Hilfst du mir mal?” Mit breitem Grinsen entgegnete sie: ”Nichts lieber als das.” Und schubste mich durch das Portal.'
  }),
  methods: {
    validate: function(input) {
      if (input == undefined) {
        this.answered = false;
        return false;
      }
      input = input.toUpperCase();
      if (btoa(input) == "U09OTkVOUklORw==") {
        this.answered = true;
      } else {
        this.answered = false;
      }
      
      return this.answered
    }
  }
};
</script>

